import React, { useState, useEffect } from 'react';
import '../index.css';
export default function () {
    // eslint-disable-next-line
    alt.on('storage:load', (data) => {
        setStorage(data);
    });
    // let data = {
    //     name: 'STORAGE 1',
    //     size: 100000,
    //     price: 50000,
    // };
    // useEffect(() => {
    //     setStorage(data);
    // });
    const [storage, setStorage] = useState({});
    const buyStorage = () => {
        // eslint-disable-next-line
        alt.emit('storage:buy');
    };
    const closeMenu = () => {
        // eslint-disable-next-line
        alt.emit('storage:closeMenu');
    };

    return (
        <>
            <span className="Title">
                {storage.name} {storage.size * 0.001} KG
            </span>
            <span className="Actions" onClick={buyStorage}>
                BUY STORAGE
                <div className="Value">
                    <i>$ </i>
                    {storage.price}
                </div>
            </span>
            <span className="Actions" onClick={closeMenu}>
                CLOSE
            </span>
        </>
    );
}
